import { Construct } from 'constructs';
import { ICfnConditionExpression } from './cfn-condition';
import { CfnRefElement } from './cfn-element';
import { capitalizePropertyNames } from './util';

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                               
export interface CfnRuleProps {
                                                                                                                                                                                                                                                                            
  readonly ruleCondition?: ICfnConditionExpression;

                                                                                                   
  readonly assertions?: CfnRuleAssertion[];
}

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            
export class CfnRule extends CfnRefElement {
  private ruleCondition?: ICfnConditionExpression;
  private assertions?: CfnRuleAssertion[];

                                                                                                                   
  constructor(scope: Construct, id: string, props?: CfnRuleProps) {
    super(scope, id);

    this.ruleCondition = props && props.ruleCondition;
    this.assertions = props && props.assertions;
  }

                                                                                                                                                             
  public addAssertion(condition: ICfnConditionExpression, description: string) {
    if (!this.assertions) {
      this.assertions = [];
    }

    this.assertions.push({
      assert: condition,
      assertDescription: description,
    });
  }

  /**
   * @internal
   */
  public _toCloudFormation(): object {
    return {
      Rules: {
        [this.logicalId]: {
          RuleCondition: this.ruleCondition,
          Assertions: capitalizePropertyNames(this, this.assertions),
        },
      },
    };
  }
}

                            
export interface CfnRuleAssertion {
                               
  readonly assert: ICfnConditionExpression;

                                           
  readonly assertDescription: string;
}
